import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"

import Layout from "../components/layout"
import SEO from "../components/seo"

// Components
import ContactPerson from "../components/ContactPerson/ContactPerson.component";

// Styles
import "./contact-us.styles.scss";

const ContactUs = () => {

  const data = useStaticQuery(graphql`
    query {
      contact: allMarkdownRemark(filter: {frontmatter: {title: {eq: "Contact Us"}}}) {
        edges {
          node {
            frontmatter {
              title
              description
              mainTitle
              subtitle
              headImage {
                childImageSharp {
                  fluid (maxWidth: 1920) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
            html
          }
        }
      }
      contactPerson: allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(contact-us)/"}}, sort: {fields: frontmatter___order, order: ASC}) {
        edges {
          node {
            frontmatter {
              title
              jobTitle
              email
              tel
            }
          }
        }
      }
    }
  `)
  
  return (
    <Layout>
      <SEO title="Contact Us" />
      {
        data.contact.edges.map(({node}) => (
          <div>
            <SEO title={ node.frontmatter.title } description={ node.frontmatter.description } />
            <Img fluid={ node.frontmatter.headImage.childImageSharp.fluid } />
          </div>
        ))
      }
      <div className="contact-us">
      {
        data.contact.edges.map(({node}) => (
          <div>
            <h2>{ node.frontmatter.mainTitle }</h2>
            <p>{ node.frontmatter.subtitle }</p>
          </div>
        ))
      }
      <div className="contact-group">
        {
          data.contactPerson.edges.map(({node}) => (
            <ContactPerson
              name={ node.frontmatter.title }
              jobTitle={ node.frontmatter.jobTitle }
              email={ node.frontmatter.email }
              tel={ node.frontmatter.tel }
            />
          ))
        }
      </div>
        
      </div>
      <div className="final-contact-details">
        <div className="final-contact-details-container">
          <div className="final-contact-details-content">
            <h3>To place an order please call:</h3>
            <p>+353 1 902 0026</p>
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default ContactUs
